.#{$project_name} {
	&_confirm {
		@include fixed(0, $L:0);
		z-index: 9999;
		@include wh(100vw, 100vh);

		opacity: 0;
		z-index: -1;

		transition: $transition-base;

		&.js-show {
			opacity: 1;
			z-index: 999;
		}
		&-bg {
			@include wh;
			background-color: rgba(#000, 0.5);
		}

		&-wrap {
			@include absolute(50%, $L:50%);
			transform: translate(-50%, -50%);
			transform-origin: 0 0;
			width: 560px;
			// max-height: 90%;
			// overflow: hidden;

			opacity: 0;
			transition: $transition-base;

			@include media-breakpoint-down(xs) {
				width: 100%;
			}

			.js-show & {
				opacity: 1;
				animation-name: fadeIn;
				animation-duration: 0.5s;
			}

			// .discount_send_confirm & {
			// 	width: 600px;

			// 	@include media-breakpoint-down(sm){
			// 		width: 500px;
			// 	}
			// 	@include media-breakpoint-down(xs){
			// 		width: 315px;
			// 	}
			// }
		}

		&-container {
			position: relative;
			max-height: 90vh;
			border-radius: 15px;
			background-color: #fff;
			box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.25);

		}

		&-bd {
			padding: 1.5rem;

			&-inner {
				margin-bottom: 1.6rem;
			}

		}

		&-ft {

		}

		&-btns {
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-gap: 0;
			.demobtn {
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 0.5rem;
			}
		}

		.txt {
			@include remCalc(font-size, 28);
		}
	}

}

