.#{$project_name}_dialog {
	@include fixed(0, $L:0);
	z-index: 9999;
	@include wh(100vw, 100vh);

	opacity: 0;
	z-index: -1;

	transition: $transition-base;

	&.js-show {
		opacity: 1;
		z-index: 999;
	}
	&-bg {
		@include wh;
		background-color: rgba(#000, 0.5);
	}

	&-wrap {
		@include absolute(50%, $L:50%);
		transform: translate(-50%, -50%);
		transform-origin: 0 0;
		width: 320px;
		// max-height: 90%;
		// overflow: hidden;

		opacity: 0;
		transition: $transition-base;

		@include media-breakpoint-down(xs) {
			width: 100%;
		}

		.js-show & {
			opacity: 1;
			animation-name: fadeIn;
			animation-duration: 0.5s;
		}

		// .discount_send_confirm & {
		// 	width: 600px;

		// 	@include media-breakpoint-down(sm){
		// 		width: 500px;
		// 	}
		// 	@include media-breakpoint-down(xs){
		// 		width: 315px;
		// 	}
		// }
	}

	&-container {
		position: relative;
		max-height: 90vh;
		// border-radius: 15px;
		// background-color: #fff;
		// box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.25);


		.simplebar-mask, .container-inner {
			border-radius: 5px;
			background-color: #fff;
			box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.25);
			padding: 30px;
		}

	}

	.txt {
		font-size: 1.4rem;
		letter-spacing: 2px;
	}

	.bblens_btn {
		width: 150px;
		padding: 0.6rem;
		margin: auto;
	}


}

