.signin_frm .validation_code-pic { flex: 0 0 110px; margin-right: 0.5rem; }

.signin_frm .validation_code-reset { flex: 0 0 30px; }

.signin_frm .validation_code-reset { width: 30px; height: 30px; padding: 5px; background-color: rgba(102, 102, 102, 0); border: none; border-radius: 3px; transition: background-color 0.3s ease-in-out; cursor: pointer; }

.signin_frm .validation_code-reset:hover { background-color: rgba(102, 102, 102, 0.1); }

.signin_frm .validation_code-reset:active { transform: translate(1px, 1px); }

.signin_frm-submit { width: 100%; }

.signin_frm .inni_rcx-label { color: #3D3A39; }

.signin_frm .forget_pw { padding: 0.25rem; }

.signin_frm .forget_pw .txt { color: #17489C; position: relative; }

.signin_frm .forget_pw .txt:after { content: ''; display: block; position: absolute; right: 0; bottom: -3px; left: 0; width: 0%; height: 1px; background-color: rgba(23, 72, 156, 0); margin: auto; transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1); }

.signin_frm .forget_pw .txt:hover:after { width: 100%; background-color: #17489c; }

.forget_pw_frm .inni_frm-label { flex: 0 0 80px; }

.forget_pw_frm .inni_frm-field { flex: 0 0 calc(100% - 80px); }

.forget_pw_frm .inni_frm-error { left: 100px; }

.forget_pw_frm-submit { width: 100%; }

.signup_frm > .inner { padding: 0 1rem; }

.signup_frm .inni_frm-label { flex: 0 0 90px; }

.signup_frm .inni_frm-field { flex: 0 0 calc(100% - 90px); }

.signup_frm .inni_frm-error { left: 110px; }

.signup_frm .inni_frm-label { position: relative; text-align-last: left; }

.signup_frm .inni_required-mark { position: absolute; left: -0.8rem; }
